import React, { useState, useEffect } from "react";
import DataGrid from "../containers/DataGrid";
import UserContext from "../context/UserContext";
import { TextArea, Button, Box } from 'grommet'
import { updateConfig } from '../library/parse'

const Component = () => {
  const _user = React.useContext(UserContext);
  const [activeConfig, setActiveConfig] = useState({})

  useEffect(() => {
    setActiveConfig(_user && _user.config)
  }, [_user])
  const displayConfig = [{ name: 'Share To FB Template' , key: 'templateConfig' }]
  return (
    <>
      {
        _user &&  displayConfig.map((config, i) => {
            return <Box gap="small" direction="column">
            {config.name}
            <TextArea 
                value={activeConfig && activeConfig[config.key]}
                onChange={event => {

                    setActiveConfig({
                        ...activeConfig,
                        [config.key]: event.target.value
                    })
                }}
            />
            <Button 
                label="Update"
                primary
                onClick={() => {
                    updateConfig({
                        [config.key]: activeConfig[config.key]
                    })
                }}
            />
            </Box>
        })
      }
    </>
  );
};

export default Component;
